
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'

export default {
name: 'getMoney',
components: {
XInput,
Group,
CheckIcon
},
data () {
return {
value: '',
baseInfo: {},
type: '',
passWord: ''
}
},
created () {
this.getInfo()
},
methods: {
checkChange(type,val){
let tips = ''
if(type==0) {
tips = '未绑定微信'
}
if(type == 1) {
tips = '未绑定支付宝'
}
if(!val) {
this.$vux.toast.show({
text: tips,
position: 'middle',
type: 'warn'
})
return
}
this.type=type
},
getAll () {
this.value = this.baseInfo.balance
},
sendData(){
if(!this.value){
this.$vux.toast.show({
text: '请输入提现金额',
position: 'middle',
type: 'warn'
})
return
}
if(parseInt(this.value) > this.baseInfo.balance){
this.$vux.toast.show({
text: '提现金额不能大于余额',
position: 'middle',
type: 'warn'
})
return
}
if(!this.passWord) {
this.$vux.toast.show({
text: '请输入提现密码',
position: 'middle',
type: 'warn'
})
return
}
if(this.passWord.length < 6 || this.passWord.length > 20) {
this.$vux.toast.show({
text: '提现密码在6-20位之间',
position: 'middle',
type: 'warn'
})
return
}
if(!(/^[0-9a-zA-Z]*$/g.test(this.passWord))) {
this.$vux.toast.show({
text: '提现密码只能输入数字或字母',
position: 'middle',
type: 'warn'
})
return
}
if(!String(this.type)){
this.$vux.toast.show({
text: '请输入选择提现方式',
position: 'middle',
type: 'warn'
})
return
}
this.$http.fetchPost('/merchant/verify/pay/password',{merchantId: this.$store.state.merchantId,password: this.passWord}).then((res)=>{
if(res.data.code == 200) {
this.$http.fetchPost('/merchant/center/update/balance/withdraw',{
accountType: this.type,
amount: this.value,
merchantId: this.$store.state.merchantId
}).then((res)=>{
if(res.data.code == 200) {
this.$vux.toast.show({
text: '提现成功',
position: 'middle'
})
this.$router.go(-1)
}else{
this.$vux.toast.show({
text: res.data.message,
position: 'middle',
type: 'warn'
})
}
})
} else {
this.$vux.toast.show({
text: res.data.message,
position: 'middle',
type: 'warn'
})
}
})
},
getInfo () {
this.$http.fetchGet('/merchant/center/get/balance/withdraw',{merchantId: this.$store.state.merchantId}).then((res)=>{
this.baseInfo = res.data.data
})
},
getBack(){
this.$router.go(-1)
}
}
}



